<template>
  <div class="cms-list-layout">
    <cms-prompt collapse='true'>
      <li>展示管理员列表，用于登录平台管理，无法登录商城前台及商家管理平台</li>
      <li>管理员必须赋予角色权限，如无角色权限请移至系统->权限->角色管理中新增</li>
    </cms-prompt>
    <!-- 筛选区 -->
    <section class="cms-list-query">
      <div class="query-group">
        <label>是否启用</label>
        <el-select v-model="queryParams.enabled" @change="query">
          <el-option label="全部" value=" "></el-option>
          <el-option label="启用" :value="true"></el-option>
          <el-option label="禁用" :value="false"></el-option>
        </el-select>
      </div>
      <div class="query-group">
        <label>用户名</label>
        <el-input v-model="queryParams.username" @keyup.native.enter="query"></el-input>
      </div>
      <div class="query-group">
        <label>创建时间</label>
        <cms-picker @change="setTimer"></cms-picker>
      </div>
      <div class="query-group">
        <cms-button size="small" :colorPicker='true' @click="query">查询</cms-button>
      </div>
    </section>
    <!-- 操作区 -->
    <cms-sticky>
    <section class="cms-batch">
      <cms-button v-prem="'/admin/save'" icon="add" type="success" size="small" @click="$routerLink('/admin/save','save')">新增</cms-button>
      <cms-button v-prem="'/admin/delete'" icon="error" type="error" size="small" @click="deleteDatas()">批量删除</cms-button>
      <cms-button  icon="success" type="success" size="small" @click="batchEnabled()">批量启用</cms-button>
      <cms-button  icon="warning" type="warning" size="small" @click="batchDisabled()">批量禁用</cms-button>
    </section>
    </cms-sticky>
    <el-table :data="tableData" style="width: 100%" stripe v-loading="loading" @selection-change='getCheckItems' @row-click='checkRows' ref='dataTable'>
      <el-table-column type="selection" width="46" fixed='left'></el-table-column>
      <el-table-column prop="id" label="id" width="60"></el-table-column>
      <el-table-column prop="username" label="用户名" min-width='120'></el-table-column>
      <el-table-column prop="roleNames" label="角色" min-width="160"></el-table-column>
      <el-table-column prop="email" label="email" min-width="190"></el-table-column>
      <el-table-column prop="lastLoginIp" label="最后登录ip" min-width="150"></el-table-column>
      <el-table-column prop="enabled" label="是否启用" width="110">
        <div slot-scope="scope">
          <cms-switch v-model="scope.row.enabled" :id='scope.row.id' @change="setDisable"></cms-switch>
        </div>
      </el-table-column>
      <el-table-column prop="createTime" label="创建时间" min-width="160">
        <template slot-scope="scope">
          {{$moment(scope.row.createTime)}}
        </template>
      </el-table-column>
      <el-table-column label="操作" width='175' fixed="right" align="center">
        <div slot-scope="scope">
          <cms-button  v-prem="'/admin/update'"   icon='edit' size='small' type='info' @click.stop="$routerLink('/admin/update','update',{id:scope.row.id})">编辑</cms-button>
          <cms-button v-prem="'/admin/delete'"  icon='error' size='small' type='info' @click.stop="deleteData(scope.row.id)">删除</cms-button>
        </div>
      </el-table-column>
    </el-table>
    <cms-pagination @change="getPage" :total='total'></cms-pagination>
  </div>
</template>
<script>
import list from '@/mixins/list'
import {
  adminList,
  adminDelete,
  adminDisable,
  adminEnable
} from '@/api/admin/admin.js'
var defaultData = {
  enabled: '',
  username: '',
  startTime: '',
  endTime: '',
  size: 10,
  page: 1
}
export default {
  name: 'adminList',
  mixins: [list],
  data() {
    return {
      queryParams: Object.assign({}, defaultData)
    }
  },
  methods: {
    deleteData(id) {
      // 包装一层删除的操作
      this.deleteTableData(adminDelete, id) // 这个才是真实的删除方法
    },
    deleteDatas() {
      this.deleteTableDatas(adminDelete, this.ids) // 这个才是真实的删除方法
    },
    setTimer(timer) {
      // 设置时间区间
      this.queryParams.startTime = timer[0]
      this.queryParams.endTime = timer[1]
      this.query()
    },
    batchEnabled() {
      // 批量启用
      if (this.batchStatus) {
        adminEnable({ ids: this.ids }).then(res => {
          if (res.code === 200) {
            this.getTableData(adminList, this.queryParams)
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage('启用失败')
          }
        })
      } else {
        this.$errorMessage('请至少选择一条数据')
      }
    },
    batchDisabled() {
      // 批量禁用
      if (this.batchStatus) {
        adminDisable({ ids: this.ids }).then(res => {
          if (res.code === 200) {
            this.getTableData(adminList, this.queryParams)
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage('禁用失败')
          }
        })
      } else {
        this.$errorMessage('请至少选择一条数据')
      }
    },
    setDisable(status, id) {
      if (status) {
        adminEnable({ ids: [id] }).then(res => {
          // 启用
          if (res.code === 200) {
            this.$successMessage('启用成功')
          } else {
            this.$errorMessage('启用失败')
          }
        })
      } else {
        adminDisable({ ids: [id] }).then(res => {
          // 禁用
          if (res.code === 200) {
            this.$successMessage('禁用成功')
          } else {
            this.$errorMessage('禁用失败')
          }
        })
      }
    }
  },
  activated() {
    // 缓存触发
    if (this.$route.query.type === 'list') {
      this.queryParams = Object.assign({}, defaultData)
      // 这个值一定要用动态的变量
      this.getTableData(adminList, this.queryParams)
    }
  },
  created() {
    // 首次触发
    this.getTableData(adminList, this.queryParams)
  }
}
</script>

<style>
</style>



